<template>
    <section class="sign-in-page">
        <div class="alert text-white bg-danger" role="alert" v-bind:style="{ display }">
            <div class="iq-alert-icon">
                <i class="ri-information-line"></i>
            </div>
            <div class="iq-alert-text">SIlahkan cek <b>Email</b> atau <b>Password</b> anda!</div>
        </div>
        <div class="container sign-in-page-bg mt-2 p-0">
            <div class="row no-gutters">
                <div class="col-md-6 text-center">
                    <div class="sign-in-detail text-white">
                        <a class="sign-in-logo mb-5" href="#"><img src="images/logoW.png" class="img-fluid"
                                alt="logo"></a>
                        <div class="owl-carousel" data-autoplay="true" data-loop="true" data-nav="false"
                            data-dots="true" data-items="1" data-items-laptop="1" data-items-tab="1"
                            data-items-mobile="1" data-items-mobile-sm="1" data-margin="0">
                            <div class="item">
                                <img src="images/login/1.png" class="img-fluid mb-4" alt="logo">
                                <h4 class="mb-1 text-white">Monitoring Kapal</h4>
                                <p>Memonitoring kapal anda secara realtime.</p>
                            </div>
                            <div class="item">
                                <img src="images/login/2.png" class="img-fluid mb-4" alt="logo">
                                <h4 class="mb-1 text-white">Manage Kapal</h4>
                                <p>Mengatur dan memonitoring data kapal.</p>
                            </div>
                            <div class="item">
                                <img src="images/login/3.png" class="img-fluid mb-4" alt="logo">
                                <h4 class="mb-1 text-white">Manage Akun Kapal</h4>
                                <p>Membuat dan mengatur akun perusahaan.</p>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-md-6 position-relative">
                    <div class="sign-in-from">
                        <h1 class="mb-0">Sign in</h1>
                        <p>Enter your email address and password to access admin panel.</p>
                        <form class="mt-4" v-on:submit.prevent="loginAuth">
                            <div class="form-group mt-4">
                                <label for="exampleInputEmail1">Email address</label>
                                <input type="email" class="form-control mb-0" 
                                    v-model.trim="email" 
                                    placeholder="Enter email" 
                                    :class="{'is-invalid': validationStatus($v.email)}">
                                <div v-if="!$v.email.required" class="invalid-feedback">Harap mengisi Email terlebih dahulu !</div>
                                <div v-if="!$v.email.email" class="invalid-feedback">Penulisan Email tidak sesuai dengan format email semestinya !</div>
                            </div>
                            <div class="form-group">
                                <label for="exampleInputPassword1">Password</label>
                                <a href="#" class="float-right">Forgot password?</a>
                                <input type="password" class="form-control mb-0" 
                                    v-model.trim="password" 
                                    placeholder="Password" 
                                    :class="{'is-invalid': validationStatus($v.password)}">
                                <div v-if="!$v.password.required" class="invalid-feedback">Harap mengissi Password terlebih dahulu !</div>
                            </div>
                            <div class="d-inline-block w-100">
                                <div class="custom-control custom-checkbox d-inline-block mt-2 pt-1">
                                    <input type="checkbox" class="custom-control-input" id="customCheck1">
                                    <label class="custom-control-label" for="customCheck1">Remember Me</label>
                                </div>
                                <button class="btn btn-primary float-right">Sign in</button>
                            </div>
                            <div class="sign-info">
                                <span class="dark-color d-inline-block line-height-2">Don't have an account? <a
                                        href="#">Sign up</a></span>
                                <ul class="iq-social-media">
                                    <li><a href="#"><i class="ri-facebook-box-line"></i></a></li>
                                    <li><a href="#"><i class="ri-twitter-line"></i></a></li>
                                    <li><a href="#"><i class="ri-instagram-line"></i></a></li>
                                </ul>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
    import axios from "axios";
    import { required, email } from 'vuelidate/lib/validators'

    export default {
        name: 'Login',
        data() {
            return {
                email: "",
                password: "",
                display: 'none'
            };
        },
        validations: {
            email: {required, email},
            password: {required}
        },
        beforeCreate: function () {
            if (!this.$session.exists()) {
                this.$router.push('/sign');
            }else{
                if(this.$session.get('level')=='root'){
                    this.$router.push('/data_perusahaan')
                }else{
                    this.$router.push('/perusahaan')
                }
            }
        },
        methods: {
            validationStatus: function(validation) {
                return typeof validation != "undefined" ? validation.$error : false;
            },
            async loginAuth() {
                this.$v.$touch();
                if (this.$v.$pendding || this.$v.$error) return;
                try {
                    const result = await axios.post("https://track.kapalpintar.co.id/api/login", {
                        email: this.email,
                        password: this.password
                    });
                    if (result.data.status == 1) {
                        this.$session.start();
                        if(result.data.results[0].level=="root"){
                            this.$session.set("nama_perusahaan", result.data.results[0].nama_perusahaan);
                        }
                        this.$session.set("token", result.data.results[0].id_customer);
                        this.$session.set("email", result.data.results[0].email);
                        this.$session.set("level", result.data.results[0].level);
                        this.$router.go();
                        // console.log(result.data.results[0].id_perusahaan)
                    } else if(result.data.status == 0){
                        this.display = "inline-flex";
                        window.setTimeout(() => {
                            this.display = "none";
                        }, 3000);
                    }
                } catch (err) {
                    console.log(err)
                }
            }
        }
    }
</script>

<style>
    .alert {
        position: absolute;
        right: 10px;
        display: none;
    }
</style>