import Vue from 'vue'
import VueRouter from "vue-router"
import LoadScript from 'vue-plugin-load-script';
import Vuelidate from 'vuelidate';
import VueSession from 'vue-session'
import { VuejsDatatableFactory } from 'vuejs-datatable'
import Vue2LeafletRotatedMarker from 'vue2-leaflet-rotatedmarker'
import vueTopprogress from 'vue-top-progress'
import VueSweetalert2 from 'vue-sweetalert2';
import DataTable from 'laravel-vue-datatable';

//data tabel required
import "datatables.net-bs4"
import "jszip"
import "pdfmake"
import "datatables.net-buttons-bs4"
import "datatables.net-responsive-bs4"

import 'leaflet/dist/leaflet.css';
import 'sweetalert2/dist/sweetalert2.min.css';

import App from "./App.vue"
// import Index from "./components/Content.vue"
import Login from "./components/Login.vue"
import Perusahaan from "./components/Perusahaan.vue"
import Data_perusahan from "./components/Data_perusahaan.vue"

Vue.use(VueRouter)
Vue.use(LoadScript);
Vue.use(Vuelidate);
Vue.use( VuejsDatatableFactory );
Vue.component('v-rotated-marker', Vue2LeafletRotatedMarker)
Vue.use(vueTopprogress)
Vue.use(VueSweetalert2);
Vue.use(DataTable);

var options = {
  persist: true
}
Vue.use(VueSession, options)

Vue.config.productionTip = false

const routes = [
  {
    name: 'Index',
    path: '/',
    component: Login
  },
  {
    name: 'Sign',
    path: '/sign',
    component: Login
  },
  {
    name: 'Data Perusahaan',
    path: '/data_perusahaan',
    component: Data_perusahan
  },
  {
    name: 'Perusahaan',
    path: '/perusahaan',
    component: Perusahaan
  }
];

const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

const router = new VueRouter({ mode: 'history', routes: routes})

new Vue({
  // init router
  router,
  render: h => h(App),
}).$mount('#app')
