<template>
    <div class="wrapper">
        <SlideLeft />
        <div id="content-page" class="content-page">
            <TopBar />
            <div class="container-fluid">

                <div class="row">
                    <div class="col-lg-9">
                        <div class="iq-card iq-card-block iq-card-stretch">
                            <div class="iq-card-body pb-0">
                                <div class="row">
                                    <div class="col-lg-12">
                                        <div class="iq-card mb-0" v-if="tgldari.length">
                                            <div class="iq-card-header d-flex justify-content-between p-0 bg-white">
                                                <div class="iq-header-title">
                                                    <div class="row">
                                                        <div class="col-lg-6">
                                                            <div class="form-group">
                                                                <label>Filter Tanggal Dari</label>
                                                                <select class="form-control " v-model.trim="dari">
                                                                    <option value="">Pilih Tnggal Dari</option>
                                                                    <option v-for="tgl in tgldari" :key="tgl.VAR_MOID"
                                                                        :value="tgl.tanggal">{{tgl.tanggal}}</option>
                                                                </select>
                                                            </div>
                                                        </div>
                                                        <div class="col-lg-6">
                                                            <div class="form-group">
                                                                <label>Filter Tanggal Sampai</label>
                                                                <select class="form-control " v-model.trim="sampai">
                                                                    <option value="">Pilih Tanggal Sampai</option>
                                                                    <option v-for="tgl in tgldari" :key="tgl.VAR_MOID"
                                                                        :value="tgl.tanggal">{{tgl.tanggal}}</option>
                                                                </select>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>

                                                <div class="iq-card-header-toolbar d-flex align-items-center">
                                                    <button type="button" class="btn btn-lg mt-3 btn-primary"
                                                        @click="getHistoriKapal">
                                                        Tampilkan <i class="ri-file-search-line"></i></button>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="iq-card p-0">
                                            <div class="iq-card-bodyn p-0">
                                                <l-map style="height: 595px" :zoom="zoom" :center="center"
                                                    @fullscreenchange="fullscreenChanged">
                                                    <l-polyline :lat-lngs="latlngData ? latlngData: 0" :color="color">
                                                    </l-polyline>
                                                    <l-tile-layer :url="url" :attribution="attribution"></l-tile-layer>
                                                    <l-geo-json :geojson="geojson"></l-geo-json>
                                                    <l-rotated-marker v-if="marker.length" :lat-lng="marker"
                                                        :icon="iconOn"
                                                        :rotationAngle="parseInt(heading) ? parseInt(heading) : 0">
                                                        <l-popup>
                                                            <table>
                                                                <tr>
                                                                    <td class="font-weight-bold">Nama Kapal</td>
                                                                    <td>: </td>
                                                                    <td> {{ nm_kapal }}</td>
                                                                </tr>
                                                                <tr>
                                                                    <td class="font-weight-bold">Device ID</td>
                                                                    <td>: </td>
                                                                    <td>{{ deviceID }}</td>
                                                                </tr>
                                                                <tr>
                                                                    <td class="font-weight-bold">Last Update</td>
                                                                    <td>: </td>
                                                                    <td :class="{'text-danger': timestamp < tglNow}">
                                                                        {{ timestamp }}</td>
                                                                </tr>
                                                                <tr>
                                                                    <td class="font-weight-bold">Speed</td>
                                                                    <td>: </td>
                                                                    <td>{{ speed ? speed: 0 }}</td>
                                                                </tr>
                                                                <tr>
                                                                    <td class="font-weight-bold">Imei</td>
                                                                    <td>: </td>
                                                                    <td>{{ imei ? imei : '0' }}</td>
                                                                </tr>
                                                                <tr>
                                                                    <td class="font-weight-bold">Atp Start / Atp End
                                                                    </td>
                                                                    <td>: </td>
                                                                    <td>{{ atpstart }} / {{atpend}}</td>
                                                                </tr>
                                                            </table>
                                                        </l-popup>
                                                    </l-rotated-marker>
                                                    <!-- all kapal marker -->
                                                    <l-rotated-marker v-for="data in markerAllKapal" :key="data.VAR_MOID"
                                                        :lat-lng="[data.lat, data.lon]"
                                                        :icon="data.speed ? iconOn  : iconOff"
                                                        :rotationAngle="parseInt(data.heading) ? parseInt(data.heading) : 0">
                                                        <l-popup>
                                                            <table>
                                                                <tr>
                                                                    <td class="font-weight-bold">Nama Kapal</td>
                                                                    <td>: </td>
                                                                    <td> {{ data.name }}</td>
                                                                </tr>
                                                                <tr>
                                                                    <td class="font-weight-bold">Device ID</td>
                                                                    <td>: </td>
                                                                    <td>{{ data.sn }}</td>
                                                                </tr>
                                                                <tr>
                                                                    <td class="font-weight-bold">Speed</td>
                                                                    <td>: </td>
                                                                    <td :class="{'text-danger': data.speed === 0}">
                                                                        {{ data.speed ? data.speed : 0  }}</td>
                                                                </tr>
                                                                <tr>
                                                                    <td class="font-weight-bold">Last Update</td>
                                                                    <td>: </td>
                                                                    <td :class="{'text-danger': data.timestamp < data.tglNow}">{{ data.timestamp }}</td>
                                                                </tr>
                                                                <tr>
                                                                    <td class="font-weight-bold">Atp Start/Apt End</td>
                                                                    <td>: </td>
                                                                    <td>{{ data.atp_strat ?data.atp_strat:0 }} / {{data.atp_end ? data.atp_end : 0}}</td>
                                                                </tr>
                                                            </table>
                                                        </l-popup>
                                                    </l-rotated-marker>
                                                    
                                                    <!-- histori kaal marker -->
                                                    <l-rotated-marker v-for="data in markers" :key="data.VAR_MOID"
                                                        :lat-lng="[data.Latitude, data.Longitude]"
                                                        :icon="data.kecepatan ? iconOn  : iconOff"
                                                        :rotationAngle="parseInt(data.Direction) ? parseInt(data.Direction) : 0">
                                                        <l-popup>
                                                            <table>
                                                                <tr>
                                                                    <td class="font-weight-bold">Network</td>
                                                                    <td>: </td>
                                                                    <td> {{ data.network }}</td>
                                                                </tr>
                                                                <tr>
                                                                    <td class="font-weight-bold">Device Type</td>
                                                                    <td>: </td>
                                                                    <td>{{ data.deviceType }}</td>
                                                                </tr>
                                                                <tr>
                                                                    <td class="font-weight-bold">Speed</td>
                                                                    <td>: </td>
                                                                    <td :class="{'text-danger': data.kecepatan === 0}">
                                                                        {{ data.kecepatan ? data.kecepatan : 0  }}</td>
                                                                </tr>
                                                                <tr>
                                                                    <td class="font-weight-bold">Carrier Flag</td>
                                                                    <td>: </td>
                                                                    <td>{{ data.carrierFlag }}</td>
                                                                </tr>
                                                            </table>
                                                        </l-popup>
                                                    </l-rotated-marker>
                                                    <l-control-fullscreen position="topleft"
                                                        :options="fullscreenOptions" />
                                                </l-map>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="col-lg-3 mail-box-detail">
                        <div class="iq-card">
                            <div class="iq-card-body p-0">
                                <div class="d-flex p-3 justify-content-between">
                                    <div class="form-group mb-0" style="width: 100%;">
                                        <input type="text" 
                                            class="form-control" 
                                            placeholder="Cari nama perusahaan"
                                            style="padding-right: 25px;"
                                            v-model="cariperusahaan"
                                            @keyup="cariNamaperusahaan">
                                        <i class="ri-search-line" style="position: absolute;right: 40px;top: 28px;"></i>
                                    </div>
                                </div>
                                <h6 class="ml-4 mb-3 font-weight-bold">Daftar Perusahaan</h6>
                                <div class="iq-email-listbox" style="height: 485px;overflow-x: auto;">
                                    <div class="tab-content" style="margin-top: -15px;">
                                        <div class="tab-pane fade show active" id="mail-inbox" role="tabpanel">
                                            <ul class="iq-email-sender-list">
                                                <li v-for="value in dataPerusahaanAll" :key="value.id" class="iq-unread"
                                                    style="background: transparent;">
                                                    <div class="d-flex align-self-center">
                                                        <div class="iq-email-sender-info">
                                                            <span class="ri-building-fill mr-0"></span>
                                                            <a href="javascript: void(0);"
                                                                class="iq-email-title left mr-3"
                                                                @click="getDetail(value.id, value.customer_name)">{{value.customer_name}}</a>
                                                        </div>
                                                        <ul class="iq-social-media" style="background: transparent;">
                                                            <li>
                                                                <a href="#" title="Detail">
                                                                    <i class="ri-file-list-2-line"></i>
                                                                </a>
                                                            </li>
                                                        </ul>
                                                    </div>
                                                </li>
                                                <div class="email-app-details" :class="showDetail" style="height:0px">
                                                    <div class="iq-card">
                                                        <div class="iq-card-body p-0"
                                                            style="height: 599px;overflow-x: auto;">
                                                            <div class="iq-email-to-list p-3 m-0 ml-2"
                                                                style="overflow-x: hidden;">
                                                                <div class="d-flex justify-content-between">
                                                                    <ul>
                                                                        <li class="mr-1">
                                                                            <a href="javascript: void(0);"
                                                                                @click="backKapal">
                                                                                <h4 class="m-0"><i
                                                                                        class="ri-arrow-left-line"></i>
                                                                                </h4>
                                                                            </a>
                                                                        </li>
                                                                        <li class="ml-3 pt-2">
                                                                            <h6 class="font-modif font-weight-bold">
                                                                                Daftar Kapal Perusahan</h6>
                                                                        </li>
                                                                    </ul>
                                                                </div>
                                                            </div>
                                                            <h6 class="ml-3 mb-2 mt-0" style="font-weight: 500;">
                                                                {{nm_perusa}}</h6>
                                                            <div class="iq-search-bar">
                                                                <form action="#" class="searchbox"
                                                                    style="width: 290px;">
                                                                    <input type="text" class="form-control"
                                                                        placeholder="Cari nama kapal..."
                                                                        v-model="search" @keyup="seacrhNamakapal">
                                                                    <a class="search-link" href="javascript:void(0)"
                                                                        style="top:10px"><i
                                                                            class="ri-search-line"></i></a>
                                                                </form>
                                                            </div>

                                                            <div class="iq-inbox-subject">
                                                                <div class="tab-pane fade show active" role="tabpanel">
                                                                    <ul class="iq-email-sender-list">
                                                                        <li v-for="data in Kapal" :key="data.sn">
                                                                            <div class="d-flex align-self-center">
                                                                                <div class="iq-email-sender-info">
                                                                                    <span
                                                                                        class="ri-ship-2-fill mr-0"
                                                                                        :class="{'text-danger': data.timestamp < data.tglNow}"></span>
                                                                                    <a href="javascript: void(0);"
                                                                                        class="iq-email-title left mr-3"
                                                                                        @click="selectItem(data)">
                                                                                        {{ data.name }}
                                                                                    </a>
                                                                                </div>
                                                                            </div>
                                                                        </li>

                                                                    </ul>
                                                                </div>
                                                            </div>
                                                        </div>

                                                        <div class="iq-card-footer d-flex border-top">
                                                            <div class="col-lg-12">
                                                                <div class="row">
                                                                    <div class="col-md-6 pl-3 p-2 text-left">
                                                                        Total Data : <b>{{totalrow}}</b>
                                                                    </div>
                                                                    <div class="col-md-6 pr-3 p-2 text-right">

                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>

                                                    </div>
                                                </div>
                                            </ul>
                                        </div>

                                    </div>
                                </div>
                                <div class="iq-card-footer d-flex border-top">
                                    <div class="col-lg-12">
                                        <div class="row">
                                            <div class="col-md-6 pl-3 p-2 text-left">
                                                Total Data : <b>{{totalrowPerusahaan}}</b>
                                            </div>
                                            <div class="col-md-6 pr-3 p-2 text-right">

                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                
            </div>
            <Footer />
        </div>
    </div>
</template>

<script>
    import axios from "axios";
    import SlideLeft from "./views/SlideLeft.vue"
    import TopBar from "./views/TopBar.vue"
    import Footer from "./views/Footer.vue"

    //maps
    import {
        LMap,
        LTileLayer,
        LGeoJson,
        LPopup,
        LPolyline
    } from 'vue2-leaflet';
    import dataJSON from '../assets/maps.json';
    import {
        Icon
    } from 'leaflet';
    import L from 'leaflet';
    import LControlFullscreen from "vue2-leaflet-fullscreen";

    delete Icon.Default.prototype._getIconUrl;
    Icon.Default.mergeOptions({
        iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
        iconUrl: require('leaflet/dist/images/marker-icon.png'),
        shadowUrl: require('leaflet/dist/images/marker-shadow.png'),
    });
    import Vue2LeafletRotatedMarker from 'vue2-leaflet-rotatedmarker'

    export default {
        name: 'Perusahaan',
        components: {
            SlideLeft,
            TopBar,
            Footer,
            LMap,
            LTileLayer,
            LGeoJson,
            LPopup,
            LPolyline,
            'l-rotated-marker': Vue2LeafletRotatedMarker,
            LControlFullscreen
        },
        data() {
            return {
                //root
                dataPerusahaanAll: [],

                id_perusahaan: this.$session.get('token'),
                Nmperusahaan: '',
                email: this.$session.get('email'),
                NmSPlit: '',
                perusahaan: '',
                phone: '',
                contact: '',
                address: '',
                Kapal: [],
                selectedItem: null,
                deviceID: null,
                tgldari: [],
                dari: '',
                sampai: '',
                search: '',
                cariperusahaan: '',
                totalrow: 0,
                totalrowPerusahaan: 0,
                showDetail: '',
                nm_perusa: '',
                idPerusahaan: null,

                //popup detail
                nm_kapal: null,
                timestamp: null,
                speed: null,
                tglNow: null,
                atpstart: null,
                atpend: null,
                imei: null,

                //data maps
                url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
                attribution: '&copy; <a href="https://kapalpintar.co.id">Kapal Pintar</a> contributors',
                zoom: 4,
                geojson: null,
                center: [-1.9309741, 120.7177424],

                markers: [],
                marker: [],
                markerAllKapal: [],
                heading: 0,
                iconOn: L.icon({
                    iconUrl: './images/iconon.png',
                    iconSize: 10,
                }),
                iconOff: L.icon({
                    iconUrl: './images/iconoff.png',
                    iconSize: 10,
                }),
                staticAnchor: [16, 37],
                customText: 'Foobar',
                iconSize: 10,
                path: "/images/",
                fullscreenOptions: {
                    title: {
                        'false': 'Switch to full-screen view',
                        'true': 'Exit full-screen mode',
                    },
                },
                latlngData: [],
                color: 'gray'
            }
        },
        beforeCreate: function () {
            if (!this.$session.exists()) {
                this.$router.push('/sign');
            }else{
                this.$router.push('/data_perusahaan')
            }
        },
        mounted() {
            this.getNmPerusahaan();
            this.loadMaps();
            this.getPerusahaanAll();
        },
        computed: {
            dynamicSize() {
                return [this.iconSize, this.iconSize * 1.15];
            },
            dynamicAnchor() {
                return [this.iconSize / 2, this.iconSize * 1.15];
            }
        },
        created: function () {},
        methods: {
            async getNmPerusahaan() {
                try {
                    if (this.$session.get('level') == 'root') {
                        this.perusahaan = this.$session.get('nama_perusahaan');
                        this.Nmperusahaan = "Admin";
                    } else {
                        const response = await axios.get(
                            `https://track.kapalpintar.co.id/api/perusahaan/${this.$session.get('token')}`);
                        this.perusahaan = response.data.results[0].customer_name;
                        this.NmSPlit = this.perusahaan.split(" ");
                        this.Nmperusahaan = this.NmSPlit[0];
                        this.phone = response.data.results[0].phone;
                        this.contact = response.data.results[0].contact;
                        this.address = response.data.results[0].address;

                    }
                } catch (err) {
                    console.log(err);
                }
            },
            //root
            async getPerusahaanAll() {
                this.$swal({
                    html: '<h6>Loading... </h6><img style="width:50px" src="./images/loading-bar.gif"/>',
                    width: 300,
                    showConfirmButton: false,
                });
                try {
                    await axios
                        .get('https://track.kapalpintar.co.id/api/data_perusahaan')
                        .then((response) => {
                            this.dataPerusahaanAll = response.data.results;
                            this.totalrowPerusahaan = this.dataPerusahaanAll.length;
                        })
                } catch (err) {
                    console.log(err);
                }
                this.$swal({
                    showConfirmButton: false
                }).close();
            },
            async getDetail(id, name_perusahaan) {
                this.search = '';
                this.showDetail = 'show';
                this.$swal({
                    html: '<h6>Loading... </h6><img style="width:50px" src="./images/loading-bar.gif"/>',
                    width: 300,
                    showConfirmButton: false,
                });
                try {
                    await axios
                        .get('https://track.kapalpintar.co.id/api/kapal/' + id)
                        .then((response) => {
                            this.Kapal = response.data;
                            this.markerAllKapal = response.data;
                            this.totalrow = response.data.length;
                            this.idPerusahaan = id;
                            this.nm_perusa = name_perusahaan;
                        })
                } catch (err) {
                    console.log(err);
                }
                this.$swal({
                    showConfirmButton: false
                }).close();
            },
            async cariNamaperusahaan() {
                await axios
                    .post('https://track.kapalpintar.co.id/api/data_perusahaan/search/', {
                        headers: 'Access-Control-Allow-Origin: *',
                        mode: 'cors',
                        search: this.cariperusahaan
                    })
                    .then(response => {
                        this.dataPerusahaanAll = response.data;
                        this.totalrowPerusahaan = response.data.length;
                    }, (err) => {
                        console.log(err);
                    })
            },
            async seacrhNamakapal() {
                await axios
                    .post(`https://track.kapalpintar.co.id/api/kapal/search/${this.idPerusahaan}`, {
                        headers: 'Access-Control-Allow-Origin: *',
                        mode: 'cors',
                        search: this.search
                    })
                    .then(response => {
                        this.Kapal = response.data;
                        this.totalrow = response.data.length;
                    }, (err) => {
                        console.log(err);
                    })
            },
            async backKapal() {
                this.showDetail = '';
                this.tgldari = [];
                this.marker = [];
                this.markers = [];
                this.zoom = 4;
            },
            async getHistoriKapal() {
                this.$swal({
                    html: '<h6>Loading... </h6><img style="width:50px" src="./images/loading-bar.gif"/>',
                    width: 300,
                    showConfirmButton: false,
                });
                await axios
                    .post(`https://track.kapalpintar.co.id/api/histori_kapal/${this.deviceID}`, {
                        headers: 'Access-Control-Allow-Origin: *',
                        mode: 'cors',
                        dari: this.dari,
                        sampai: this.sampai
                    })
                    .then((response) => {
                        this.marker = [];
                        this.markers = response.data.histori;
                        this.latlngData = response.data.polyline;
                    }, (err) => {
                        console.log(err)
                    })
                this.$swal({
                    showConfirmButton: false
                }).close();
            },
            selectItem(item) {
                this.$swal({
                    html: '<h6>Loading... </h6><img style="width:50px" src="./images/loading-bar.gif"/>',
                    width: 300,
                    showConfirmButton: false,
                });
                this.markerAllKapal = [];
                this.zoom = 4;
                this.selectedItem = item;
                this.deviceID = item.sn;
                this.nm_kapal = item.name;
                this.timestamp = item.timestamp;
                this.speed = item.speed;
                this.atpstart = item.atm_start;
                this.atpend = item.atm_end;
                this.imei = item.imei;
                this.marker = [parseFloat(item.lat), parseFloat(item.lon)];
                this.center = [parseFloat(item.lat), parseFloat(item.lon)];
                this.markers = [];
                this.latlngData = [];
                this.heading = parseInt(item.heading);
                this.tglNow = item.tglNow;
                this.getTanggal();
            },
            loadMaps() {
                // const response = await fetch('https://apitrack.kapalpintar.co.id/maps/seazone.json');
                this.geojson = dataJSON;
            },
            async getTanggal() {
                try {
                    await axios
                        .get(`https://track.kapalpintar.co.id/api/histori_tgl/${this.deviceID}`)
                        .then((response) => {
                            this.tgldari = response.data;
                        })
                } catch (err) {
                    console.log(err);
                }
                this.$swal({
                    showConfirmButton: false
                }).close();
            },
            fullscreenChanged() {},
        }
    }
</script>

<style>
    .selectedRow {
        background: #f2f2f2 !important;
        color: #000;
    }

    .pointer {
        cursor: pointer;
    }

    .vue2leaflet-map {
        z-index: 0;
    }

    .table td,
    .table th {
        padding: 5px !important;
    }

    .border-top {
        border-top: 1px solid #eeeeee;
    }

    .left {
        left: 45px !important;
    }

    .font-modif {
        font-size: 11pt;
        margin-top: 4px;
    }
</style>